<template>
    <v-container fluid class="container pa-0">
        <play-part-exercise
            :motionCamera_playZone="motionCamera_playZone"
            v-on:close-on="ResetPlayItems()"
            v-on:close-skeleton="PushSkeletonOn()"
        />
        <common-confirm-dialog
            :dialog="reconnetDialog"
            :dialogItems="dialogItems"
            v-on:close-cancel="CancelDialog()"
            v-on:close-allow="AllowDialog()"
        />
    </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import common from '@/mixins/common.js'
import motionCamera from '@/mixins/motionCamera.js'

export default {
    name: 'PartPlay',
    mixins:[ common, motionCamera ],
    data:()=>({
        dialogItems:{
            title: null,
            text: null,
            close: '취소',
            confirm: '확인'
        },
        reconnetDialog: false
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_MENU',
            'GET_MENU_SELECT_EXERCISE',
            'GET_PART_PLAY_TOTAL_EXERCISE_TIME',
            'GET_PART_PLAY_TOTAL_REST_TIME',
            'GET_PART_PLAY_EXERCISE_MODE',
            'GET_PART_PLAY_TOTAL_SET',
            'GET_PART_PLAY_PLAY_SET',
            'GET_PART_PLAY_WEIGHT',
            'GET_PART_PLAY_PLAY_ITEMS',
            'GET_PART_PLAY_EXERCISE_TIME',
            'GET_PART_PLAY_GOAL_COUNT',
        ])
    },
    watch:{
        async GET_PART_PLAY_PLAY_SET(value){
            if(value > 0){
                await this.PushResultData()
                await this.RecodeDetail()
                if(value === this.GET_PART_PLAY_TOTAL_SET){
                    await this.PushCompleteData()
                }else{
                    await this.EditPlayExercise()
                }
            }
        },
        motionCamera_exercise_count(){
            this.MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT(this.GET_PART_PLAY_PLAY_ITEMS.exerciseCount + 1)
            if(this.GET_PART_PLAY_PLAY_ITEMS.exerciseCount === this.GET_PART_PLAY_GOAL_COUNT){
                this.MU_PART_PLAY_PLAY_SET(this.GET_PART_PLAY_PLAY_SET + 1)
            }
        },
        motionCamera_dialog(value){
            if(value !== null){
                this.statusStop = true
                this.motionCamera_requestToDisconnect()
                this.dialogItems = {
                    title: value.title,
                    text: value.message,
                    close: '취소',
                    confirm: '연결 재시도'
                }
                this.reconnetDialog = true
            }
        },
        motionCamera_webSocket_play(value){
            if(!value){
                this.MU_PART_PLAY_EXERCISE_MODE(0)
                this.EditPlayExercise()
            }
        }
    },
    created() {
        this.CheckMotionMode()
    },
    mounted() {
        this.EditPlayExercise()
    },
    destroy(){
        this.ResetPlayItems()
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_PART_TOOL',
            'ACT_PART_PLAY_API_SET_PART_RESULT',
            'ACT_PART_PLAY_API_SET_PART_COMPLETE',
        ]),
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE_MODE',
            'MU_PART_PLAY_EXERCISE_TIME_MODE_TIME',
            'MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT',
            'MU_PART_PLAY_REST_SET',
            'MU_REPORT_EXERCISE_TIME',
            'MU_REPORT_RESULT_DETAIL',
            'MU_REPORT_ITEMS',

            // Reset
            'MU_PART_PLAY_EXERCISE',
            'MU_PART_PLAY_EXERCISE_TIME',
            'MU_PART_PLAY_TOTAL_EXERCISE_TIME',
            'MU_PART_PLAY_TOTAL_REST_TIME',
            'MU_PART_PLAY_TOTAL_SET',
            'MU_PART_PLAY_PLAY_SET',
            'MU_PART_PLAY_WEIGHT',
            'MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT',
            'MU_PART_PLAY_GOAL_COUNT',
            'MU_PART_PLAY_PLAY_ITEMS',
        ]),
        CheckMotionMode(){
            let exerciseMode = 0 // 사용자 입력 모드
            
            if( this.GET_MENU_SELECT_EXERCISE.exMode === 1 ){
                exerciseMode = 1 // Time 모드
            }else{
                // env 확인
                const envMotionMode = process.env.VUE_APP_MOTION_CARMERA_MODE
                // motion 운동 확인
                const check = this.motionCamera_checkExercise(this.GET_MENU_SELECT_EXERCISE.exSortCode)

                if(envMotionMode === 'true' && check){
                    exerciseMode = 2 // motion 모드
                    this.motionCamera_open(this.GET_MENU_SELECT_EXERCISE.exSortCode)
                }
            }
            this.MU_PART_PLAY_EXERCISE_MODE(exerciseMode)
        },
        EditPlayExercise(){
            this.EditRestSet()
            this.MU_PART_PLAY_GOAL_COUNT(10) // 목표 횟수 설정
        },
        EditRestSet(){
            let restSet = 0
            // Time 모드 예외처리
            if(this.GET_PART_PLAY_EXERCISE_MODE === 1){
                restSet = 5
                this.MU_PART_PLAY_TOTAL_SET(1)
            }else{
                restSet = 20
                this.MU_PART_PLAY_TOTAL_SET(1)
            }
            this.MU_PART_PLAY_REST_SET(restSet)
        },
        RecodeDetail(){
            let recode
            // Time 모드 예외처리
            if(this.GET_PART_PLAY_EXERCISE_MODE === 1){
                recode = `${this.GET_PART_PLAY_PLAY_SET}세트 | ${this.GET_PART_PLAY_EXERCISE_TIME}`
            }else{
                recode = `${this.GET_PART_PLAY_PLAY_SET}세트 | ${this.GET_PART_PLAY_PLAY_ITEMS.exerciseCount}`
            }
            // set 운동 count reset
            this.MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT(0)
            
            let detailItems = {
                title: this.GET_MENU_SELECT_EXERCISE.name,
                success: true,
                recode: recode
            }

            let reportItems = {
                totalSet: this.GET_PART_PLAY_TOTAL_SET,
                exerciseSet: this.GET_PART_PLAY_PLAY_SET
            }
            
            this.MU_REPORT_EXERCISE_TIME(this.GET_PART_PLAY_EXERCISE_TIME)
            this.MU_REPORT_RESULT_DETAIL(detailItems)
            this.MU_REPORT_ITEMS(reportItems)
        },
        AllowDialog(){
            this.dialogItems    = {}
            this.reconnetDialog = false
            this.CheckMotionMode()
            this.EditPlayExercise()
        },
        CancelDialog(){
            this.dialogItems    = {}
            this.reconnetDialog = false
            this.MU_PART_PLAY_EXERCISE_MODE(0)
        },
        PushSkeletonOn(){
            console.log('push skeleton')
            this.motionCamera_requestToStopRecognition()
        },
        async PushResultData(){
            let repetitionsNumber = []

            let select = this.GET_PART_PLAY_PLAY_ITEMS.exerciseCount

            // Time모드 기록 예외처리
            if(this.GET_PART_PLAY_EXERCISE_MODE === 1){
                select = 1
            }else{
                for(let i = 0; i < select; i++){
                    let detailData = {
                        weight      : this.GET_PART_PLAY_WEIGHT,
                        moveRange   : 0
                    }
                    repetitionsNumber.push(detailData)
                }
            }

            let setData = {
                setStartTime: this.GET_PART_PLAY_PLAY_ITEMS.setStartTime,
                setSeq      : this.GET_PART_PLAY_PLAY_SET,
                repetitions : select,
                exTime      : this.GET_PART_PLAY_TOTAL_EXERCISE_TIME,
                restTime    : this.GET_PART_PLAY_TOTAL_REST_TIME,
                setDetail   : repetitionsNumber
            }
            await this.ACT_PART_PLAY_API_SET_PART_RESULT(setData)
        },
        async PushCompleteData(){
            let params = {}

            // Time모드 예외처리
            if(this.GET_PART_PLAY_EXERCISE_MODE === 1){
                params={
                    exSortCode: this.GET_MENU_SELECT_EXERCISE.exSortCode,
                    isPresEx: 1
                }
            }else{
                params={
                    exSortCode: this.GET_MENU_SELECT_EXERCISE.exSortCode,
                    isPresEx: 0
                }
            }
            await this.ACT_PART_PLAY_API_SET_PART_COMPLETE(params)
            .then(()=>{
                // 되돌아갈 Link
                const firstLink = this.common_back_link()
                this.$router.push(firstLink)
            })
            // this.ResetPlayItems()
        },
        ResetPlayItems(){
            // WebSocket 종료 예외처리
            if(this.GET_PART_PLAY_EXERCISE_MODE === 2 || !this.motionCamera_webSocket_play){
                this.motionCamera_requestToStopRecognition()
                this.motionCamera_requestToDisconnect()
            }
            // 초기화
            const exerciseTime = '00:00'
            const totalExerciseTime = 0
            const totalRestTime = 0
            const totalSet = 0
            const playSet = 0
            const playItems = {
                setStartTime: 0,
                exerciseTotalCount: 0,
                exerciseCount: 0,
                detailItems:[]
            }

            this.MU_PART_PLAY_EXERCISE_MODE(0)
            this.MU_PART_PLAY_EXERCISE_TIME_MODE_TIME(25)
            this.MU_PART_PLAY_EXERCISE(false)
            this.MU_PART_PLAY_EXERCISE_TIME(exerciseTime)
            this.MU_PART_PLAY_TOTAL_EXERCISE_TIME(totalExerciseTime)
            this.MU_PART_PLAY_TOTAL_REST_TIME(totalRestTime)
            this.MU_PART_PLAY_TOTAL_SET(totalSet)
            this.MU_PART_PLAY_PLAY_SET(playSet)
            this.MU_PART_PLAY_WEIGHT(0)
            this.MU_PART_PLAY_GOAL_COUNT(0)
            this.MU_PART_PLAY_PLAY_ITEMS(playItems)
        },
    },
}
</script>

<style lang="scss">
.play_contents{
    height: 87%;
}
</style>